import React, { Component, lazy } from "react";
import { Link } from "react-router-dom";
import { formatDate, localStoragedecryp, localStorageencryp } from "../../../../components/helpers/function";
import { ToastNotification } from "../../../../components/helpers/notification";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../../components/panel/panel";
import { postData } from "../../../../config/axios";
import * as moment from "moment-timezone";
const HeadFormLaporanPembelianBarang = lazy(() =>
  import("./HeadFormLaporanPembelianBarang")
);


class FormLaporanPembelianBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanTransaksiPembelian: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
      tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
    };

    postData("pembelian/get/report", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            export: false,
            isLoading: false,
            LaporanTransaksiPembelian : []
          });
        } else {
          ToastNotification("success", "Laporan Transaksi Pembelian Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            isLoading: false,
            LaporanTransaksiPembelian: res.data,
          });
        }
      })
     
      .catch((error) => {
        this.setState({
          export: false,
          isLoading: false
        });
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        // this.setState({ isLoading: false });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">
            Laporan Transaksi Pembelian
          </li>
        </ol>
        <h1 className="page-header">Laporan Transaksi Pembelian </h1>
        <Panel>
          <PanelHeader>Laporan Transaksi Pembelian</PanelHeader>
          <PanelBody>
            {/* {console.log(this.state.hidden)} */}
            <HeadFormLaporanPembelianBarang
              export={this.state.export}
              isLoading={this.state.isLoading}
              LaporanTransaksiPembelian={this.state.LaporanTransaksiPembelian}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}
export default FormLaporanPembelianBarang;
